<template>
  <div>
    <ul>
      <li class="logoli">
        <img src="@/assets/imgs/logo.png" width="100%" alt="logo" />
      </li>
      <li>
        <el-input
          v-model="logininfo.user_id"
          placeholder="请输入账户"
          clearable
          prefix-icon="el-icon-user-solid"
          maxlength="4"
          autocomplete="off"
        ></el-input>
      </li>
      <li>
        <el-input
          v-model="logininfo.user_key"
          placeholder="请输入密码"
          show-password
          clearable
          prefix-icon="el-icon-s-tools"
          @keyup.enter.native="dologin"
        ></el-input>
      </li>
      <li>
        <el-button @click="dologin" type="primary" :loading="loading" icon="el-icon-s-custom">登 录</el-button>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import jwt from "jsonwebtoken";

export default {
  data() {
    return {
      token: "",
      logininfo: {
        user_id: "",
        user_key: ""
      },
      loading: false,
      input_err: true
    };
  },
  methods: {
    input_is_ok() {
      this.input_err = false;
      if (this.logininfo.user_id.length != 4) {
        this.$message("账号至少4位");
        this.input_err = true;
      }

      if (this.logininfo.user_key.length < 6) {
        this.$message("密码至少6位");
        this.input_err = true;
      }
    },
    dologin() {
      //提交登录
      this.input_is_ok(); //检测输入值
      (async () => {
        if (!this.input_err && this.loading == false) {
          this.loading = true;
          //输入没有错误 并且 没有在等待反馈
          axios({
            url: require("@/config").axios.login,
            method: "post",
            data: { uid: this.logininfo.user_id, key: this.logininfo.user_key }
          })
            .then(async re => {
              if (re.data.code == 200) {
                const token = jwt.sign(
                  re.data.user,
                  require("@/config").secret,
                  {
                    expiresIn: 60 * 30 * 5
                  }
                ); //创建TOKEN

                sessionStorage.setItem("token", token); //保存token
                this.$router.push("mp"); //登录成功，跳转到主页
              } else {
                //code 404 用户不存在 code 500 密码错误，并不反馈给用户
                this.$message(`登录失败，请核对账户及密码！`);
              }
              this.loading = false;
            })
            .catch(err => {
              this.loading = false;
            });
        }
      })();
    }
  }
};
</script>

<style scoped>
div img {
  opacity: 0.8;
  box-sizing: border-box;
  border: 8rem solid rgba(0, 0, 0, 0);
}
div li {
  padding: 8px 0;
}

div button {
  width: 100%;
}
</style>